import {
	mapState
} from 'vuex';
export default {
	data() {
		return {
			addressData: {
				name1: "",
				name2: "",
				name3: "",
				name4: "",
				name5: "",
			}
		};
	},
	methods: {
		// 提交表单
		confirm() {
			let data = this.addressData;
			// data.nickName的nickName是自己取的 控制台可显示 因为他是那全局的（userInfo.nickName来默认显示）
			data.nickName = this.userInfo.nickName;
			console.log(JSON.stringify(data))
		},
	},
	computed: {
		...mapState(['userInfo']) 
	},
	mounted() {
		console.log(this.userInfo)
	}
}  
